* {
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #000000;
  line-height: 1.5;
  font-size: 22px;
}

#search-box {
  font: inherit;
  color: inherit;
  padding: 8px 20px;
  max-width: 320px;
  margin-bottom: 16px;
  font-size: 20px;
  border: 2px solid teal;
  border-radius: 50px;
  background-color: #ffffff;
}

#search-box::placeholder {
  color: #787c7b;
  font-size: 16px;
  vertical-align: middle;
}

.country-info-list {
  margin-bottom: 20px;
}

.country-item {
  display: flex;
  align-items: center;
}

.country-item + .country-item {
  margin-top: 5px;
}

.country-name {
  margin-left: 10px;
  font-size: 27px;
}

.country-name-big {
  font-size: 27px;
  font-weight: 700;
}

.country-info__text {
  font-weight: 700;
}

.country-info + .country-info {
  margin-top: 15px;
}
